<script>
import { clickOutside } from "../../libraries/clickOutside";
import { turpial } from "../../libraries/turpial";
import DatePicker from "../DatePicker/DatePicker.svelte";
import LocalFilter from "./LocalFilter.svelte";

let container_clickoutside = window.container_clickoutside_dropdowns ?? document;

export let is_loaded = true;
export let size = "236px;";
export let triggerStyle = "";
export let innerType = false;
export let name = "dropdown-"+turpial.random_string(6);
export let input_classes = "formElements";
export let selected = null;
export let label = "";
export let label_id = "label-"+turpial.random_string(6);
export let has_input_for_names = false;
export let has_simple_search = false;
export let has_new_version = false;


export let on_mount = function(){};
export let on_change = function(){};
/**
 * executes on_change or on_mount events.
 * @returns {{field_name: string, name: string, value: string}}
 */
export let on_update = function(){};



let field_name = name;

/*
* versions
* is-medium, is-small
* if we dont set version always will be default
*/
export let classes = "default";

export let classes_alt = "";
export let classes_container = "is-normal";
export let isSearch = false;
export let CustoStoreData = {};
export let list = null;
export let showLabel = false;
export let has_state = false;
export let itemStyles = {
    bg: "background: #f5f8fa;",
    color: "has-text-grey",
    icon: "icon-tripod-warning"
};
export let has_white_bg = false;
export let is_currency = false;
export let is_for_calendar = false;

let from_date;
let until_date;
let new_array = [];
let parent_container = null;
let dropdown_element;
let isChanged = false;

/**
 * @type {HTMLElement}
 */
let parentContainerItems = null;

list.map(( item )=>{
    try {
        if(typeof item.value !== "undefined" && typeof item.name !== "undefined"){
            new_array.push(item)
            return;
        }
    } catch (error) {
        
    }

    new_array.push({
        name: item,
        value: item,
    }) 
})

list = new_array;

if(has_new_version){
    has_new_version = true;
}


let selected_item = null;
let object_item_selected = null;
let selected_value = null;
let data_result = selected;
let is_searching = false;

function is_writing(value){
    is_searching = value;
}

function ProcessList(){
    let has_selected = false;
    list.map(( item )=>{     
        if(has_selected === true){ return; }
        if(item.value === selected){
            has_selected = true;
            selected_item = item.name;
            selected_value = item.value;
            object_item_selected = item;
        }else{
            if(!is_searching){
                selected_item = list[0].name;
                selected_value = list[0].value;
                object_item_selected = list[0];
            }
        }
    })

    on_mount(selected_item, selected_value);

    on_update({
        field_name: field_name,
        name: selected_item,
        value: selected_value,
        item: object_item_selected
    })

    isChanged = true;
    data_result = selected_value;
}

ProcessList();

$: {
    if(selected && is_for_calendar){
        ProcessList();
    }
}

$: {
    if((list != null)){
        if (isChanged) {
            ProcessList();
        }
    }
}

let trigger;
let containerList;

/**
 * A function that is called when the user clicks on the dropdown. 
 * It adds the class is-drop-active to the dropdown.
*/
function handdleShowMore(element) {
    if (!element.classList.contains('is-drop-active')) {
        closeTabs(element.parentNode);
        element.classList.add("is-drop-active");
    }else{
        element.classList.remove("is-drop-active");
    }
    return;
}

/**
 * A function that closes all the dropdowns that are open.
*/
function closeTabs(container){
    if (innerType) {
        let containers = [...container.parentNode.parentNode.querySelectorAll(".is-dropdown-inner")];
        containers.map(( node )=>{
            node.classList.remove("is-drop-active");
        })
    }else{
        let containers = [...container.querySelectorAll(".is-dropdown")];
        containers.map(( node )=>{
            node.classList.remove("is-drop-active");
        })
    }
}


/**
 * A function that is called when the user clicks on an item in the dropdown.
 * It changes the value of the dropdown to the value of the item clicked.
*/
function change(name, value, event, item = null, has_code = false){
    is_writing(false);

    isChanged = true;
    if (has_code) {
        selected = value
    }else{
        selected = name;
    }
    data_result = value;
    selected_item = name;
    selected_value = value;

    dropdown_element.classList.remove("is-drop-active");

    on_change(name, value, event, item);
        
    on_update({
        field_name: field_name,
        name: name,
        value: value,
        event: event,
        item: item
    })
}

$: {
    if(parent_container !== null){
        clickOutside([parent_container, containerList], (destroy)=>{
            try {
                dropdown_element.classList.remove("is-drop-active");
            } catch (error) {
                destroy();
            }
            
        }, container_clickoutside)
    }
}

/**
 * getOpener(): get function to open date picker range.
*/
function getOpener(opener){
    DatePickerOpener = opener;
}

</script>
<style>
.container-dropdown{
    display: flex;
    grid-column-gap: 18px;
    flex-direction: column;
    align-items: baseline;
}
.is-dropdown.white .trigger{
    background-color: #fff;
    box-shadow: 5px 5px 32px hsl(0deg 0% 56% / 10%);
}
.is-dropdown .trigger{
    min-width: 150px;
    max-width: 237px;
    width: 100%;
    border-radius: 3px;
    
    /* max-width: max-content; */
}
.label-container{
    margin-right: 1rem;
}
.item-dropdown{
    background-color: #fff;
    transition: all 0.15s ease;
    min-width: 100%;
    color: #424B54;
}
.item-dropdown:hover{
    background-color: var(--color-lightGrey);
    transition: all 0.15s ease;
}
.item-dropdown:first-child{
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
}
.item-dropdown:last-child{
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
}
.container-items{
    max-height: 195px;
    overflow-y: auto;
    overflow-x: hidden;
    z-index: 30;
}
.seach-container{
    padding: 0.625rem 0.625rem 0px 0.625rem;
    background: white;
    z-index: 1000;
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
}

.message-item{
    display: none;
    position: absolute;
    top: 0;
    z-index: 1000;
    width: fit-content;
    background-color: #fff;
    box-shadow: 5px 5px 32px hsl(0deg 0% 56% / 10%);
    border-radius: 20px;
    font-size: 0.75em;
    padding: 0.5rem 1rem;
    word-break: break-word;
}


.is-dropdown p:global(.active){
    display: block;
}

.is-dropdown .trigger.inherit-width{
    width: inherit;
    max-width: inherit;
}

.is-dropdown .container-items.inherit-width{
    width: inherit;
    max-width: inherit;
}

/* Styles for medium and small size versions */
.is-dropdown.is-medium,
.is-dropdown.is-small{
    width: 100%;
    padding: 0 !important;
}

.is-dropdown.is-medium .trigger,
.is-dropdown.is-small .trigger{
    min-width: 100%;
    max-height: 37px; 
    padding: 0 0.9375rem;
}

.is-dropdown.is-medium .item-dropdown,
.is-dropdown.is-small .item-dropdown{
    min-width: 100%;
    font-weight: 500;
    padding-left: 0.5rem;
}

.is-dropdown.is-medium .container-items,
.is-dropdown.is-small .container-items{
    padding: 0.625rem;
    max-height: 145px;
}

.is-dropdown.is-small .trigger > i,
.is-dropdown.is-medium .trigger > i{
    font-size: 7px !important;
}

.is-dropdown.is-medium{
    max-width: 135px;
}

.is-dropdown.is-small{
    max-width: 87px;
}

.is-dropdown.is-small > .container-items, 
.is-dropdown.is-medium > .container-items{
    min-width: auto;
}


@media only screen and (max-width: 900px){
    [class*="has-background-white-touch"]{
        border: 1px solid var(--color-extralightGrey);
    }
}

@media only screen and (max-width: 700px){
    .container-dropdown{
        flex-direction: column;
        align-items: baseline;
    }
}

</style>
<div class="container-dropdown {classes_container} full-width" bind:this="{parent_container}">
    <div class="label-dropdown {!showLabel ? 'is-hidden' : ""} ">
        <label class="has-text-dark fw600" for="{label_id}" style="white-space: nowrap;">
            {label}
        </label>
    </div>
    <div class="is-dropdown full-width {classes}" bind:this="{dropdown_element}" style="{`width: ${size}`}">
        <input type="hidden" id={label_id} bind:value="{data_result}" name="{name}" class="{input_classes}">
        {#if has_input_for_names === true}
            <input type="hidden" bind:value="{selected_item}" name="{name+"_name"}" class="{input_classes}">
        {/if}
        {#if has_state}
            <button  type="button" bind:this="{trigger}" class="trigger {classes_alt} font-size-14 fw500" style="padding: 0 10px; {triggerStyle} {itemStyles.bg} {has_white_bg === false ? "" : "background: white; border: 1px solid #B4C0C9;"}" on:click|stopPropagation|preventDefault={(e)=>{handdleShowMore(e.currentTarget.parentNode)}}>
                <div class="flex-center gap-17">
                    <i class="{itemStyles.icon} font-size-24 {itemStyles.color}"></i>
                    {#if (selected_item === null)}
                        <slot></slot>
                        {:else}
                            <p class="{itemStyles.color} font-size-14 fw500">{selected_item}</p>
                    {/if}
                </div>
                <i class="icon-tripod-arrow-small-down is-dropdown-icon {itemStyles.color}" style="font-size: 10px;"></i>
                <!-- <div class="is-dropdown-icon"></div> -->
            </button>
        {:else}
            <button type="button" bind:this="{trigger}" class="trigger {classes_alt} font-size-14 fw500" style="{triggerStyle} {has_white_bg === false ? "" : "background: white; border: 1px solid #B4C0C9;"}" on:click|stopPropagation|preventDefault={(e)=>{handdleShowMore(e.currentTarget.parentNode)}} disabled={!is_loaded && true}>   
                {#if (selected_item === null)}
                    <slot></slot>
                    {:else}
                    <div class="flex-center gap-14">
                        {#if is_currency}
                            <img src="https://cdn.jsdelivr.net/npm/country-flag-emoji-json@2.0.0/dist/images/{selected_value}.svg" alt="country-flag" style="width: 22px;">
                        {/if}
                        <p class="white-nowrap" style="text-overflow: ellipsis; overflow: hidden; max-width: {trigger.clientWidth - 65}px;">
                            {selected_item}
                        </p>
                    </div>
                {/if}
                <i class="icon-tripod-arrow-small-down is-dropdown-icon" style="font-size: 10px;"></i>
                <!-- <div class="is-dropdown-icon"></div> -->
            </button>
        {/if}
        <!-- svelte-ignore a11y-mouse-events-have-key-events -->
        <ul bind:this="{containerList}" class="container-items {classes_alt} inherit-width custom-scrollbar-gray {isSearch ? "has-search-container" : ""}">
            {#if isSearch}
                <div class="seach-container flex-center">
                    <LocalFilter parentContainerItems={containerList} method="using-dom" on_change={change} is_writing={is_writing} has_simple_search={has_simple_search}></LocalFilter>
                </div>
            {/if}
            {#each list as item}
                <div class="item-container">
                    <li class="item-dropdown pointer is-flex gap-14 font-size-14" data-value="{item.value}" on:click|preventDefault="{function(event){{ change(item.name, item.value, event, item) }}}">
                        {#if is_currency}
                            <img src="{item.flag}" alt="country-flag" style="width: 22px;">
                        {/if}
                        {item.name}
                    </li>
                </div>
            {/each}
        </ul>
        <p class="message-item"></p>
    </div>
</div>
