<script>
import { CustomStore } from '../../libraries/customStore/CustomStore';
import ComponentsGroup from '../ComponentsGroup.svelte';
import BaseLocationsDropdown from './BaseLocationsDropdown.svelte';
import LocalFilter from './LocalFilter.svelte';

export let on_change = ()=>{};
export let on_update = ()=>{};
export let avaiable_countries = [];
export let selected = {
    name: "Colombia",
    value: "COL"
}
export let size = "236px;";
export let has_white_bg = false;
export let is_full_width = false;
export let name = "country";
export let input_classes = "formElements"

export let showLabel = false;
export let label = "";

let is_loaded = false;

let CountriesDropdown = new CustomStore(
    {
        countiresData: [],
        resultSeach: []
    },
    function(){
        CountriesDropdown = CountriesDropdown;
    }
)

try {
    if((window?.all_countries ?? null) === null){
        fetch(`https://cdn.jsdelivr.net/gh/Yerikmiller/Countries-States-Cities-JSON@latest/all_only_countries.json`)
        .then((r)=>{
            return r.json();
        }).then((countries)=>{
            
            if(Array.isArray(avaiable_countries)){
                if(avaiable_countries.length > 0){
                    countries = countries.filter((country)=>{
                        let available = false;
                        avaiable_countries.map((code3)=>{
                            if(country.iso3 === code3){
                                available = true;
                            }
                        })

                        if(available){
                            return country;
                        }
                    })
                }
            }


            getCountriesArray(countries);
            window.all_countries = countries;
            is_loaded = true;
        })
    }else{
        getCountriesArray(window?.all_countries);  
        is_loaded = true;
    }
    
} catch (error) {
    
}

function getCountriesArray(countiresData) {
    let processArray = [];
    if(Array.isArray(countiresData)){
        for (const value of countiresData) {
            processArray.push({
                name: value.translations.es ?? value.name,
                value: value.iso3,
                valueAlt: value.iso2
            })
        }

        let iso2Country = processArray.find((country) => {
            if(country.value === selected.value){
                return country;
            }
        });

        window.change_country = iso2Country.valueAlt;
        CountriesDropdown.setJson("countiresData", processArray);
        CountriesDropdown.setJson("resultSeach", processArray)
    }
}

</script>
<style>
</style>

<ComponentsGroup is_full_width={is_full_width}>
    <BaseLocationsDropdown
        has_input_for_names={true}
        size={size}
        has_white_bg={has_white_bg}
        CustoStoreData={CountriesDropdown}
        is_loaded={is_loaded}
        input_classes={input_classes}
        name="{name}"
        label_id="country"
        classes_alt={"py-0 px-4 inherit-width has-background-white-touch"}
        selected={selected.value}
        isSearch={true} 
        label="{label}"
        showLabel={showLabel}
        on_change={on_change}
        on_update={on_update}
        list={CountriesDropdown.data.resultSeach}
        triggerStyle="max-width: 100% !important; width: 100%;"
    >
        {selected.name}
    </BaseLocationsDropdown>
</ComponentsGroup>