<script>
export let gap = "16px";
export let classes = "is-flex is-block-touch is-align-items-center";
export let is_full_width = false;
export let styles_container = "";

</script>
<div class="components-group {classes} columns is-multiline {is_full_width ? 'is-full-width' : ''}" style="grid-column-gap: {gap}; position:relative; {styles_container}">
    <slot></slot>
</div>

<style>

    .columns:not(:last-child){
        margin-bottom: -0.75rem !important;
    }
    .is-full-width{
        width: 100%;
    }
    @media only screen and (max-width: 560px){
        .components-group{
            display: inline-block !important;
        }
        .components-group.is-full-width{
            display: flex !important;
        }
    }
</style>

