<script>
import  DropdownsLocations  from 'tripod-styles/src/components/Dropdowns/DropdownsLocations.svelte';
import Styles from './Styles.svelte';
import { ShadowRoot } from './libraries/ShadowRoot';
import { turpial } from 'tripod-styles/src/libraries/turpial';
import { onMount } from 'svelte';

let SvelteModule = null;
let loaded = false;
window.DropdownsLocations = SvelteModule;
onMount(()=>{
	
	ShadowRoot.update(SvelteModule)
	window.container_clickoutside_dropdowns = $ShadowRoot;

	turpial.linkCSS({
		target: $ShadowRoot,
		url: [
			`https://cdn.jsdelivr.net/gh/Yerikmiller/3pod-us.github.io@master/icons/platform/tripod-pack/tripod-pack.css`,
		], ready: ()=>{
			loaded = true;
		}
	})

})
</script>
<section class="svelte-module" bind:this={SvelteModule}>
	{#if loaded}
		<Styles />
		<DropdownsLocations user={{}}></DropdownsLocations>
	{/if}
</section>