<script>
import InsideContriesLocations from './InsideContriesLocations.svelte';
import InsidesLocationsDropdowns from './InsidesLocationsDropdowns.svelte';

export let locationObject = {
    code_country_3: "COL",
    state: "Cundinamarca",
    state_code: "CUN",
    city: "Agua de Dios"
};

export let input_names = {
    country: "country",
    state: "state",
    city: "city",
}

export let avaiable_countries = [];
export let states_cities_url = "https://cdn.jsdelivr.net/gh/Yerikmiller/Countries-States-Cities-JSON@latest/countries/";


if(typeof window.locationObjectCountry !== "undefined"){
    locationObject = window.locationObjectCountry;
}

if(typeof window.avaiable_countries_selector !== "undefined"){
    avaiable_countries = window.avaiable_countries_selector;
}

if(typeof window.states_cities_url !== "undefined"){
    states_cities_url = window.states_cities_url;
}

if(typeof window.dropdowns_locations === "undefined"){
    window.dropdowns_locations = {};
    window.dropdowns_locations.inputs = {
        country: "country",
        state: "state",
        city: "city",
    }
    /**
     * define a functions from outside to catch all object on changes.
     */
    window.dropdowns_locations.on_change_country = (properties)=>{
        console.log(properties)
    };
    window.dropdowns_locations.on_change_state = (properties)=>{
        console.log(properties)
    };
    window.dropdowns_locations.on_change_city = (properties)=>{
        console.log(properties)
    };
}



let defaultCountry = locationObject?.code_country_3 ?? "COL";
let selectedCountry = {
    value: locationObject?.company_code_country_3 ?? defaultCountry
};
let selectedState = {
    name: locationObject?.state ?? "Cundinamarca",
    value: locationObject?.state_code ?? "CUN"
}

let selectedStateCities = selectedState

let selectedCity = {
    name: "Ciudad",
    value: locationObject?.city ?? ""
}

window.dropdowns_locations.on_change_country({
    name: locationObject.code_country_3,
    value: locationObject.code_country_3,
})
window.dropdowns_locations.on_change_state({
    name: locationObject.state,
    value: locationObject.state_code,
})
window.dropdowns_locations.on_change_city({
    name: locationObject.city,
    value: locationObject.city,
})

let proviciesUrl = `${states_cities_url}${defaultCountry}.json`;

function on_change_countries(props){
    if(typeof props?.value === "string"){
        proviciesUrl = `${states_cities_url}${props.value}.json`;
        props.proviciesUrl = proviciesUrl;
        
        try {
                window.dropdowns_locations.on_change_country(props)
            } catch (error) {
                
        }
    }
    
}

function on_change_provicies(props){
    if(typeof props?.value === "string"){
        selectedStateCities.name = props.name;
        selectedStateCities.value = props.value;
        try {
            window.dropdowns_locations.on_change_state(props)
        } catch (error) {}
    }
}

function on_change_cities(props){

    if(typeof props?.value === "string"){
        try {
            window.dropdowns_locations.on_change_city(props)
        } catch (error) {
            
        }
    }
}
</script>

<dropdown-locations-container class="full-width flex-column is-gap-10">
    <div class="full-width flex-center is-gap-10">
        <div class="full-width">
            <!-- country dropdown -->
            <InsideContriesLocations 
                on_update={on_change_countries}
                size="100%"
                has_white_bg={true}
                selected={selectedCountry}
                avaiable_countries={avaiable_countries}
                name={input_names.country}
                is_full_width={true}
                showLabel={true}
                label={"País"}
            ></InsideContriesLocations>
        </div>
        <div class="full-width">
            <!-- state dropdown -->
            <InsidesLocationsDropdowns size="100%"
                has_white_bg={true}
                type={input_names.state}
                queryUrl={proviciesUrl}
                on_update={on_change_provicies}
                on_change={on_change_provicies}
                selected={selectedState}
                is_full_width={true}
                showLabel={true}
                label={"Provincia"}
            ></InsidesLocationsDropdowns>
        </div>
    </div>
    <!-- city dropdown -->
    <div class="full-width flex-center">
        <InsidesLocationsDropdowns
            size="100%"
            has_white_bg={true}
            type={input_names.city}
            queryUrl={proviciesUrl}
            on_update={on_change_cities}
            selectedState={selectedStateCities}
            selected={selectedCity}
            is_full_width={true}
            showLabel={true}
            label={"Ciudad"}
        ></InsidesLocationsDropdowns>
    </div>
</dropdown-locations-container>

<style>
dropdown-locations-container:global( .components-group){
    margin: initial !important;
}
</style>