<script>
import { onMount } from "svelte";
import { CustomStore } from "../../libraries/customStore/CustomStore";
import ComponentsGroup from "../ComponentsGroup.svelte";
import BaseLocationsDropdown from "./BaseLocationsDropdown.svelte";


export let on_change = ()=>{};
export let on_update = ()=>{};
export let queryUrl = "";
export let type = "state";
export let selected = {
    name: "Cundinamarca",
    value: "CUN"
}
console.log(selected);

export let selectedState = "";
export let size = "236px;";
export let has_white_bg = false;
export let is_full_width = false;
export let input_classes = "formElements";
export let selectedCity = null;

export let showLabel = false;
export let label = "";

let ProvinceDropdown = new CustomStore(
    {
        provincesData: [],
        resultSeach: [],
        citiesData: [],
        citiesSearche: []
    },
    function(){
        ProvinceDropdown = ProvinceDropdown;
    }
)

function update_status(has_country = false){
    fetch(queryUrl)
    .then((r)=>{
        return r.json();
    }).then((r)=>{
        if (type === "state") {
            if (r.states.length === 0) {
                getProvincesData("empty", r);
            }else{
                if (has_country) {
                    let resultState = r.states.filter((item, key)=>{
                        if (item.state_code === selected.value) {
                            window.change_state = item.state_code;
                            window.change_city = item.cities[0];
                            return item;
                        }else if (key === 0) {
                            window.change_state = item.state_code;
                            window.change_city = item.cities[0];
                            return item;
                        }
                    });
                }
                getProvincesData(r.states, "state");
                for (const value of r.states) {
                    if (value.state_code === selected.value) {
                        getProvincesData(value.cities, 'city');
                    }
                }             
            }
        }else if (type === "city") {
            if (r.states.length === 0) {
                getProvincesData("empty", r);
            }else{
                getProvincesData(r.states[0].cities);  
                for (const value of r.states) {
                    if (value.state_code === selectedState.value) {
                        getProvincesData(value.cities);  
                    }
                }
            }
        }
    })
}

$: {
    if(queryUrl !== "" || selectedState || selected){
        update_status(true);
    }
}

update_status();

function getProvincesData(provincesData, element) {
    let processArray = [];
    let citiesArray = [];

    if(provincesData === "empty"){
        processArray.push({
            name: element.name,
            value: element.iso3
        })
    }else{
        for (const value of provincesData) {
            if(type === "state"){
                if (element === "state") {
                    processArray.push({
                        name: value?.name,
                        value: value?.state_code
                    })
                }
                if (element === "city") {
                    citiesArray.push({
                        name: value?.name,
                        value: value?.state_code
                    })
                }
            }else if (type === "city") {
                citiesArray.push({
                    name: value,
                    value: value
                })
            }
        }
    }
    if (type === "state" && element === "state") {
        ProvinceDropdown.setJson("provincesData", processArray);
        ProvinceDropdown.setJson("resultSeach", processArray);
    }
    if (type === "state" && element === "city") {
        ProvinceDropdown.setJson("citiesData", citiesArray);
        ProvinceDropdown.setJson("citiesSearche", citiesArray);
    }
    if (type === "city") {
        ProvinceDropdown.setJson("provincesData", citiesArray);
        ProvinceDropdown.setJson("citiesSearche", citiesArray);
    }
    if(provincesData === "empty"){
        ProvinceDropdown.setJson("provincesData", processArray);
        ProvinceDropdown.setJson("resultSeach", processArray);
        ProvinceDropdown.setJson("provincesData", processArray);
        ProvinceDropdown.setJson("citiesSearche", processArray);
    }
}

</script>
<style>
</style>

<ComponentsGroup is_full_width={is_full_width}>
    <BaseLocationsDropdown
        has_input_for_names={true}
        size={size} has_white_bg={has_white_bg}
        CustoStoreData={ProvinceDropdown}
        input_classes={input_classes}
        name={type} 
        label_id={type}
        classes_alt={"py-0 px-4 inherit-width has-background-white-touch"}
        selected={selected.value}
        isSearch={true}
        label="{label}"
        showLabel={showLabel}
        on_change={on_change}
        on_update={on_update}
        list={type === "state" ? ProvinceDropdown.data.resultSeach : ProvinceDropdown.data.citiesSearche}
        triggerStyle="max-width: 100% !important; width: 100%;"
    >
        {selected.name}
    </BaseLocationsDropdown>
</ComponentsGroup>