<script>
export let items = {};

/**
 * using-store: set this to use a store to filter data
 * using-dom: set this if you want to filter the elements adding and removing is-hidden class. This will ensure better reactivity.
 * 
 * set a parentContainerItems to use DOM method.
 */
export let method = "using-store"; // or "using-dom";


/**
 * @type HTMLElement
 */
export let parentContainerItems = null;
export let is_writing = () => {};
export let has_simple_search = false;

let InputElement;

function filterData() {
    /**
     * @type {String}
     */
    let data = InputElement.value;


    if(method === "using-store"){
        if(data.length > 0){
            is_writing(true)
            let key = Object.keys(items.data);
            let results = items.data[key[0]].filter((item) => {
                if(item?.name?.toLowerCase().includes(data.toLowerCase())){
                    return item;
                }
            });
            
            if(items.data.hasOwnProperty('countiresData')){
                items.setJson("resultSeach", results);
            }

            if(items.data.hasOwnProperty('provincesData') && items.data?.citiesData?.length > 0){
                items.setJson("resultSeach", results);
            }
            if(items.data.hasOwnProperty('provincesData') && items.data?.citiesData?.length === 0){
                items.setJson("citiesSearche", results);
            }
            if(has_simple_search){
                items.setJson("resultSeach", results);
            }

            // if (results.length === 1 && data.toLowerCase() === results[0].name.toLowerCase()) {
            //     on_change(results[0].name, results[0].value, null, null, true);
            // }
        }else{
            if(items.data.hasOwnProperty('countiresData')){
                items.setJson("resultSeach", items.data.countiresData);
            }

            if(items.data.hasOwnProperty('provincesData') && items.data?.citiesData?.length > 0){
                items.setJson("resultSeach", items.data.provincesData);
            }
            if(items.data.hasOwnProperty('provincesData') && items.data?.citiesData?.length === 0){
                items.setJson("citiesSearche", items.data.citiesSearche);
            }
            if(has_simple_search){
                items.setJson("resultSeach", items.data.list);
            }
        }
    }


    if(method === "using-dom"){
        let items = [...parentContainerItems.querySelectorAll(".item-container li")];
        
        // remove all
        items.map(( node )=>{
            node.classList.remove("is-hidden");
            let text = node.textContent.toLowerCase().normalize('NFD').replace(/[\u0300-\u036f]/g, '');

            let content = data.toLowerCase().normalize('NFD').replace(/[\u0300-\u036f]/g, '');

            if(text.search(content) < 0){
                node.classList.add("is-hidden");
            }
        });
    }

    
}

</script>

<local-filter  class="search-container full-width">
    <input type="text" bind:this={InputElement} on:keydown={
        (event)=>{
            if( event.keyCode == 13 ) {
                event.preventDefault();
                return false;
            }
        }
    } on:input={
        ()=>{
            filterData()
        }
    } class="search-input formsElements" name="toSearch"  placeholder="Buscar">
    <span class="icon">
        <i style="font-size: 20px;" class="icon-tripod-search"></i>
    </span>
</local-filter>

<style>
.search-container{
    position: relative;
}

.search-input{
    border: 1px solid var(--color-mid-grey);
    border-radius: 8px;
    opacity: 1;
    outline: none;
    width: 100%;
    height: 32px;
}

.search-input::placeholder{
    color: var(--color-mid-grey);
    font: normal normal normal 14px/20px Montserrat;
    opacity: 1;
    width: 49px;
    height: 18px;
    transform: translateX(8px);
}

.icon{
    position: absolute;
    display: block;
    top: 5px;
    right: 9px;
    user-select: none;
}
</style>